/**
 * External dependencies
 */
import 'es6-symbol';

/**
 * Internal dependencies
 */
import { loadAlternative, loadUntestedControl } from './alternative-loading';
import { exportTrackingApi, startTracking, trackViews } from './tracking';
import { getSession } from './utils/helpers';

void ( async function (): Promise< void > {
	exportTrackingApi();

	const session = await getSession();
	if ( ! session ) {
		return loadUntestedControl();
	} //end if

	const loaded = await loadAlternative( session );
	if ( loaded ) {
		trackViews( session );
	} //end if

	startTracking( session );
} )();
