/**
 * External dependencies
 */
import isObject from 'lodash/isObject';

/**
 * Internal dependencies.
 */
import { domReady } from '../../utils/helpers';

const manageEvents = () => {
	if ( ! hasWooCommerceCartFragmentsParams( window ) ) {
		return;
	} //end if

	const previousCartData = getStoredCartData();
	const currentCartData = getWooCommerceCartData();

	const addedProducts = currentCartData.items.filter(
		( p ) =>
			! previousCartData.items.some(
				( pp ) =>
					pp.product_id === p.product_id &&
					pp.variation_id === p.variation_id
			)
	);
	if ( addedProducts.length ) {
		document.body.dispatchEvent(
			new CustomEvent( 'nab-wc-product-added-to-cart', {
				detail: [ ...addedProducts ],
			} )
		);
	} //end if

	storeCartData( { items: currentCartData.items, purchased: false } );
};

const isJQuery = ( x?: unknown ): x is JQuery =>
	!! x && 'function' === typeof x;

type JQuery = ( selector: string | Node ) => JQueryElement;
type JQueryElement = {
	readonly on: (
		event: string,
		callback: ( ...args: unknown[] ) => void
	) => void;
};

domReady( () => {
	/* eslint-disable */
	const jQuery = ( window as any ).jQuery;
	/* eslint-enable */
	if ( ! isJQuery( jQuery ) ) {
		return;
	} //end if

	jQuery( 'form.checkout' ).on( 'checkout_place_order_success', () =>
		setTimeout( () => {
			const cartData = getStoredCartData();
			storeCartData( { ...cartData, purchased: true } );
		}, 1 )
	);

	jQuery( document.body ).on(
		'wc_fragments_loaded wc_fragments_refreshed',
		() => setTimeout( manageEvents, 1 )
	);
} );

/**
 * Helpers
 */

const hasWooCommerceCartFragmentsParams = (
	x: unknown
): x is {
	// eslint-disable-next-line camelcase
	wc_cart_fragments_params: Partial< WooCommerceCartFragmentsParams >;
} =>
	hasOwnProperty( x, 'wc_cart_fragments_params' ) &&
	isObject( x.wc_cart_fragments_params );

const getStoredCartDataKey = (): string => {
	return `nab_${ getWooCommerceCartFragmentsParams().fragment_name }`;
};

const getStoredCartData = (): WooCommerceCartData => {
	const key = getStoredCartDataKey();
	if ( ! sessionStorage.getItem( key ) ) {
		storeCartData( {
			purchased: false,
			items: getWooCommerceCartData().items,
		} );
	} //end if

	try {
		const data = JSON.parse(
			sessionStorage.getItem( key ) ?? '{}'
		) as Maybe< Partial< WooCommerceCartData > >;
		return {
			purchased: !! data?.purchased,
			items: Array.isArray( data?.items ) ? data?.items ?? [] : [],
		};
	} catch ( e ) {
		return { purchased: false, items: [] };
	} //end try
};

const storeCartData = ( data: WooCommerceCartData ) => {
	sessionStorage.setItem( getStoredCartDataKey(), JSON.stringify( data ) );
};

function getWooCommerceCartData(): WooCommerceCartInfo {
	const fragmentName = getWooCommerceCartFragmentsParams().fragment_name;

	try {
		const wooCommerceFragments = JSON.parse(
			sessionStorage.getItem( fragmentName ) ?? '{}'
		) as Maybe< Partial< WooCommerceFragments > >;
		return wooCommerceFragments?.nab_cart_info ?? { items: [] };
	} catch ( e ) {
		return { items: [] };
	} //end if
} //end getWooCommerceCartData()

function getWooCommerceCartFragmentsParams(): WooCommerceCartFragmentsParams {
	const params = hasWooCommerceCartFragmentsParams( window )
		? window.wc_cart_fragments_params
		: {};

	return {
		fragment_name: '',
		...params,
	};
}

function hasOwnProperty< Y extends PropertyKey >(
	obj: unknown,
	prop: Y
): obj is Record< Y, unknown > {
	return !! obj && typeof obj === 'object' && obj.hasOwnProperty( prop );
} //end hasOwnProperty()

/**
 * Types
 */

type Maybe< T > = T | undefined;

type WooCommerceCartData = {
	readonly purchased: boolean;
	readonly items: ReadonlyArray< WooCommerceCartItem >;
};

type WooCommerceCartInfo = {
	readonly items: ReadonlyArray< WooCommerceCartItem >;
};

export type WooCommerceCartItem = {
	// eslint-disable-next-line camelcase
	readonly product_id: number;
	// eslint-disable-next-line camelcase
	readonly variation_id: number;
	readonly quantity: number;
	// eslint-disable-next-line camelcase
	readonly line_total: number;
	// eslint-disable-next-line camelcase
	readonly line_tax: number;
	// eslint-disable-next-line camelcase
	readonly line_subtotal: number;
	// eslint-disable-next-line camelcase
	readonly line_subtotal_tax: number;
};

type WooCommerceCartFragmentsParams = {
	// eslint-disable-next-line camelcase
	readonly fragment_name: string;
};

type WooCommerceFragments = {
	// eslint-disable-next-line camelcase
	readonly nab_cart_info: WooCommerceCartInfo;
};
